.Signup-form{
    margin-left: auto;
    margin-right:auto;
  }
  form{
      background-color: white;
    padding: 2em;
    padding-bottom: 3em;
    border-radius: 8px;
    max-width: 400px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: center;
    box-shadow: 0 10px 40px -14px;
    margin-left: auto;
    margin-right:auto;
    margin-top: 5em;
    
  }
  
  .form-content{
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  
  border: none;
  border-radius: 4px;
  margin-bottom: 1.2em; 
  width: 100%;
    outline: none ;
  }
  .signup-button
   {font-weight: 600;
    text-align: center;
    font-size: 19px;
    color: white;
    background-color: green;
    width: 30%;
    border: none;
    border-radius: 4px;
    padding: 0.8em;
    margin-left: 2em;
    margin-top: 1em;
    margin-bottom: 1em;
    cursor: pointer;
    overflow: hidden;
    transition: all 200ms ease-in-out;
    box-shadow: 0px 2px 6px rgba(0,0,0,0.3);
  
  }
  .button-container{
    display: inline-flex;
  }
  strong{
    padding:0em 0em 3em 0em ;
  }
  input{
    color: charcoal;
    background-color:transparent;
    box-shadow: 0 1px 0 rgba(0,0,0,0.03);
    height:2em;
  }